export const OPEN_MODAL = '[verify Code] OPEN MODAL';
export const CLOSE_MODAL = '[verify Code] CLOSE MODAL';


export const openModal = (email) => {
    return {
        type: OPEN_MODAL,
        payload: {
            email,
        }
    }
}


export const closeModal = () => {
    return {
        type: CLOSE_MODAL,
    }
}