import * as Actions from '../actions';

const initialState = {
  email: "",
  model: false,
};

const codeVerify = (state = initialState, action) => {
    switch (action.type) {
        case Actions.OPEN_MODAL: {
            return {
                ...initialState,
                ...action.payload,
                model: true
            }
        }
        case Actions.CLOSE_MODAL: {
            return initialState;
        }
        default: {
            return state;
        }
    }
};

export default codeVerify;
