import { authRoles } from 'src/app/auth';
import * as Actions from '../actions';

const initialState = {
	role: [], // guest
	data: {
		name:""
	}
};

const user = (state = initialState, action) => {
	switch (action.type) {
		case Actions.SET_USER_DATA: {
			// We Are Setting role [admin] just beacuse we dont have data from api when we have that we will set accordingly
			return {
				...initialState,
				role: action.payload.role,
				data: {
					...action.payload.data
				}
			};
		}
		case Actions.REMOVE_USER_DATA: {
			return {
				...initialState
			};
		}
		case Actions.USER_LOGGED_OUT: {
			return initialState;
		}
		default: {
			return state;
		}
	}
};

export default user;
