import * as Actions from '../actions';

const initialState = {
  open: false,
  productId: null,
  openLoginAskModel: false,
  openLoginAskTitle: null,
};

const dialogs = (state = initialState, action) => {
  switch (action.type) {
    case Actions.OPEN_CONTACT_DIALOG: {
      return {
        open: true,
        productId: action.payload,
      };
    }
    case Actions.CLOSE_CONTACT_DIALOG: {
      return {
        ...state,
        open: false,
        productId: null,
      };
    }

    case Actions.OPEN_ASK_MODEL: {
      return {
        ...state,
        openLoginAskModel: true,
        openLoginAskTitle: action.payload,
      };
    }

    case Actions.CLOSE_ASK_MODEL: {
      return {
        ...state,
        openLoginAskModel: false,
        openLoginAskTitle: null,
      };
    }

    default: {
      return state;
    }
  }
};

export default dialogs;
