import LalajiUtils from "src/@lalaji/utils";
import axios from "axios";
import jwtDecode from "jwt-decode";
import AppURL from "src/app/AppBaseURL";
import history from "src/@history";
import MyToast from "src/app/main/Shared Components/Toast";
/* eslint-disable camelcase */

class JwtService extends LalajiUtils.EventEmitter {
  init() {
    this.setBaseURL();
    this.setInterceptors();
    this.handleAuthentication();
  }

  setBaseURL = () => {
    axios.defaults.baseURL = AppURL;
  };

  setInterceptors = () => {
    axios.interceptors.response.use(
      (response) => {
        return response;
      },
      (err) => {
        return new Promise((resolve, reject) => {
          if (
            err.response &&
            err.response.status === 401 &&
            err.config &&
            !err.config.__isRetryRequest
          ) {
            // if you ever get an unauthorized response, logout the user
            this.emit("onAutoLogout", "Invalid access_token");
            this.setSession(null);
          } else if (
            err.response &&
            err.response.status >= 500 &&
            err.response.status < 600
          ) {
            MyToast("error", err?.response?.data?.message);
          }

          throw err;
        });
      }
    );
  };

  handleAuthentication = () => {
    const access_token = this.getAccessToken();

    if (!access_token) {
      this.emit("onNoAccessToken");
      return;
    }

    if (this.isAuthTokenValid(access_token)) {
      this.setSession(access_token);
      this.emit("onAutoLogin", true);
    } else {
      this.setSession(null);
      this.emit("onAutoLogout", "access_token expired");
    }
  };

  createSellerUser = (data) => {
    return new Promise((resolve, reject) => {
      axios
        .post("/user_seller/add_seller", data)
        .then((response) => {
          if (response.data) {
            this.setSession(response.data.access_token);
            resolve(response.data.user);
          } else {
            reject(response.data.error);
          }
        })
        .catch((err) => {
          reject(err.response.data.errors);
        });
    });
  };

  createBuyerUser = (data) => {
    return new Promise((resolve, reject) => {
      axios
        .post("/user_buyer/add_buyer", data)
        .then((response) => {
          if (response.data.user) {
            this.setSession(response.data.access_token);
            resolve(response.data.user);
          } else {
            reject(response.data.error);
          }
        })
        .catch((err) => {
          reject(err.response.data.errors);
        });
    });
  };

  signInWithEmailAndPassword = (username, password) => {
    return new Promise((resolve, reject) => {
      axios
        .post("/auth/login", {
          username,
          password,
        })
        .then((response) => {
          if (response.data.success) {
            const { data } = response.data;
            this.setSession(data.token);
            resolve({
              data: data.user,
              role: data.role,
            });
          } else {
            reject(response.data.error);
          }
        })
        .catch((err) => {
          MyToast("error", err.response.data);
          console.log("Error Msg =>", err.response);
        });
    });
  };

  signInWithToken = () => {
    return new Promise((resolve, reject) => {
      axios
        .get("/user_seller/current_user", {
          headers: {
            Authorization: `Bearer ${this.getAccessToken()}`,
          },
        })
        .then((response) => {
          if (response.data.success) {
            const { data } = response.data;
            this.setSession(this.getAccessToken());
            resolve({
              data: data.user,
              role: data.role,
            });
          } else {
            this.logout();
            reject(
              new Error("Failed to login with token.").toString().split(":")[1]
            );
          }
        })
        .catch((error) => {
          this.logout();
          reject(
            new Error("Failed to login with token.").toString().split(":")[1]
          );
        });
    });
  };

  signInWithCodeVerify = (email, code) => {
    return new Promise((resolve, reject) => {
      axios
        .post(`/user_seller/verify?email=${email}&code=${code}`)
        .then((response) => {
          response.data.data
            ? MyToast("success", "Email Verified!")
            : MyToast("error", "Invalid Code");
          if (response.data.data) {
            this.setSession(response.data.data.token);
            resolve({
              data: response.data.data.user,
              role: response.data.data.role,
            });
          }
        })
        .catch((err) => {
          reject(err.response.data.errors);
        });
    });
  };

  updateUserData = (user) => {
    return axios.post("/api/auth/user/update", {
      user,
    });
  };

  setSession = (access_token) => {
    if (access_token) {
      localStorage.setItem("jwt_access_token", access_token);
      axios.defaults.headers.common.Authorization = `Bearer ${access_token}`;
    } else {
      localStorage.removeItem("jwt_access_token");
      delete axios.defaults.headers.common.Authorization;
    }
  };

  logout = () => {
    this.setSession(null);
  };

  isAuthTokenValid = (access_token) => {
    if (!access_token) {
      return false;
    }
    // const decoded = jwtDecode(access_token);
    // const currentTime = Date.now() / 1000;
    // if (decoded.exp < currentTime) {
    //   console.warn("access token expired");
    //   return false;
    // }

    return true;
  };

  getAccessToken = () => {
    return window.localStorage.getItem("jwt_access_token");
  };
}

const instance = new JwtService();

export default instance;
