export const OPEN_CONTACT_DIALOG = '[DIALOG] OPEN CONTACT DIALOG';
export const CLOSE_CONTACT_DIALOG = '[DIALOG] CLOSE CONTACT DIALOG';
export const OPEN_ASK_MODEL = "[DIALOG] OPEN_ASK_MODEL[LOGIN]";
export const CLOSE_ASK_MODEL = "[DIALOG] CLOSE_ASK_MODEL[LOGIN]";

export function closeContactDialog() {
  return {
    type: CLOSE_CONTACT_DIALOG,
  };
}

export function openContactDialog(id) {
  return (dispatch, state) => {
    console.log("State => ",state());
    if  (state().auth.user.role?.length > 0) {
      dispatch({
        type: OPEN_CONTACT_DIALOG,
        payload: id,
      });;
    }  else {
      dispatch(openAskModel());
    }
  };
}

export const openAskModel = (title) => {
  return {
    type: OPEN_ASK_MODEL,
    payload: title,
  };
};

export const closeAskModel = () => {
  return {
    type: CLOSE_ASK_MODEL,
  };
};