import * as UserActions from "./index";
import history from "src/@history/index";
import axios from "axios";
import MyToast from "src/app/main/Shared Components/Toast";

export const REGISTER_ERROR = "REGISTER_ERROR";
export const REGISTER_SUCCESS = "REGISTER_SUCCESS";

export function submitSellerRegister(data) {
  return (dispatch) => {
    axios
      .post("/user_seller/add_user", data)
      .then((response) => {
        if (!response.data.data.token) {
          dispatch(UserActions.openModal(data.email));
        } else {
          localStorage.setItem("jwt_access_token", response.data.data.token);
          history.push("/");
          window.location.reload();
        }
      })
      .catch((err) => {
        MyToast("error", err?.response?.data?.message);
      });
  };
}

